'use client'

import { useState, useEffect } from 'react'
import { useUser } from '@/context/UserContext'
import { useRouter } from 'next/navigation'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'
import { Badge } from '@/components/ui/badge'
import { Alert, AlertDescription } from '@/components/ui/alert'
import { Dialog, DialogContent, DialogDescription, DialogHeader, DialogTitle, DialogTrigger } from '@/components/ui/dialog'
import { Textarea } from '@/components/ui/textarea'

interface Product {
  id: string
  title: string
  description: string
  price: number
  rentalPrice?: number
  category: string
  condition: string
  seller: string
  availability: 'available' | 'sold' | 'rented'
  views: number
}

interface CartItem {
  id: string
  product: Product
  type: 'purchase' | 'rental'
  quantity: number
  rentalDays?: number
}

interface Order {
  id: string
  items: CartItem[]
  total: number
  status: 'pending' | 'confirmed' | 'shipped' | 'delivered'
  date: string
  deliveryAddress: string
}

interface ItemRequest {
  id: string
  title: string
  description: string
  category: string
  budget: number
  duration?: string
  status: 'open' | 'fulfilled' | 'closed'
  date: string
}

export default function BuyerDashboard() {
  const { user, isAuthenticated } = useUser()
  const router = useRouter()
  const [products, setProducts] = useState<Product[]>([])
  const [cart, setCart] = useState<CartItem[]>([])
  const [wishlist, setWishlist] = useState<string[]>([])
  const [orders, setOrders] = useState<Order[]>([])
  const [requests, setRequests] = useState<ItemRequest[]>([])
  const [searchTerm, setSearchTerm] = useState('')
  const [categoryFilter, setCategoryFilter] = useState('all')
  const [typeFilter, setTypeFilter] = useState('all')
  const [isCheckingOut, setIsCheckingOut] = useState(false)
  const [isRequestingItem, setIsRequestingItem] = useState(false)
  const [deliveryAddress, setDeliveryAddress] = useState('')
  const [newRequest, setNewRequest] = useState({
    title: '',
    description: '',
    category: '',
    budget: '',
    duration: ''
  })

  useEffect(() => {
    if (!isAuthenticated || user?.role !== 'buyer') {
      router.push('/login')
      return
    }

    // Load demo data
    setProducts([
      {
        id: '1',
        title: 'Vintage Leather Jacket',
        description: 'Classic brown leather jacket in excellent condition',
        price: 45,
        rentalPrice: 8,
        category: 'Fashion',
        condition: 'Good',
        seller: 'John Seller',
        availability: 'available',
        views: 23
      },
      {
        id: '2',
        title: 'MacBook Pro 2019',
        description: '13-inch MacBook Pro with Touch Bar',
        price: 800,
        rentalPrice: 40,
        category: 'Electronics',
        condition: 'Excellent',
        seller: 'Sarah Store',
        availability: 'available',
        views: 67
      },
      {
        id: '3',
        title: 'Canon DSLR Camera',
        description: 'Professional camera with lens kit',
        price: 350,
        rentalPrice: 25,
        category: 'Electronics',
        condition: 'Very Good',
        seller: 'John Seller',
        availability: 'available',
        views: 45
      },
      {
        id: '4',
        title: 'Wooden Coffee Table',
        description: 'Handcrafted oak coffee table',
        price: 120,
        category: 'Furniture',
        condition: 'Good',
        seller: 'Mike Trader',
        availability: 'sold',
        views: 12
      },
      {
        id: '5',
        title: 'Designer Handbag',
        description: 'Authentic designer handbag, barely used',
        price: 200,
        rentalPrice: 15,
        category: 'Fashion',
        condition: 'Like New',
        seller: 'Sarah Store',
        availability: 'available',
        views: 34
      }
    ])

    setOrders([
      {
        id: '1',
        items: [],
        total: 45,
        status: 'delivered',
        date: '2024-02-28',
        deliveryAddress: '123 Main St, City, State'
      }
    ])

    setRequests([
      {
        id: '1',
        title: 'Gaming Laptop',
        description: 'Looking for a gaming laptop under $1000',
        category: 'Electronics',
        budget: 1000,
        status: 'open',
        date: '2024-03-01'
      }
    ])
  }, [isAuthenticated, user, router])

  const filteredProducts = products.filter(product => {
    const matchesSearch = product.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
                         product.description.toLowerCase().includes(searchTerm.toLowerCase())
    const matchesCategory = categoryFilter === 'all' || product.category === categoryFilter
    const matchesType = typeFilter === 'all' || 
                       (typeFilter === 'buy' && product.availability === 'available') ||
                       (typeFilter === 'rent' && product.rentalPrice && product.availability === 'available')
    
    return matchesSearch && matchesCategory && matchesType
  })

  const addToCart = (product: Product, type: 'purchase' | 'rental', rentalDays?: number) => {
    const existingItem = cart.find(item => item.product.id === product.id && item.type === type)
    
    if (existingItem) {
      setCart(cart.map(item => 
        item.product.id === product.id && item.type === type
          ? { ...item, quantity: item.quantity + 1 }
          : item
      ))
    } else {
      const newItem: CartItem = {
        id: Date.now().toString(),
        product,
        type,
        quantity: 1,
        rentalDays
      }
      setCart([...cart, newItem])
    }
  }

  const removeFromCart = (itemId: string) => {
    setCart(cart.filter(item => item.id !== itemId))
  }

  const toggleWishlist = (productId: string) => {
    if (wishlist.includes(productId)) {
      setWishlist(wishlist.filter(id => id !== productId))
    } else {
      setWishlist([...wishlist, productId])
    }
  }

  const calculateCartTotal = () => {
    return cart.reduce((total, item) => {
      const price = item.type === 'rental' && item.product.rentalPrice && item.rentalDays
        ? item.product.rentalPrice * item.rentalDays
        : item.product.price
      return total + (price * item.quantity)
    }, 0)
  }

  const handleCheckout = () => {
    if (!deliveryAddress || cart.length === 0) return

    const newOrder: Order = {
      id: Date.now().toString(),
      items: [...cart],
      total: calculateCartTotal(),
      status: 'pending',
      date: new Date().toISOString().split('T')[0],
      deliveryAddress
    }

    setOrders([newOrder, ...orders])
    setCart([])
    setDeliveryAddress('')
    setIsCheckingOut(false)
  }

  const handleItemRequest = () => {
    if (!newRequest.title || !newRequest.budget) return

    const request: ItemRequest = {
      id: Date.now().toString(),
      title: newRequest.title,
      description: newRequest.description,
      category: newRequest.category,
      budget: parseFloat(newRequest.budget),
      duration: newRequest.duration,
      status: 'open',
      date: new Date().toISOString().split('T')[0]
    }

    setRequests([request, ...requests])
    setNewRequest({
      title: '',
      description: '',
      category: '',
      budget: '',
      duration: ''
    })
    setIsRequestingItem(false)
  }

  if (!isAuthenticated || user?.role !== 'buyer') {
    return (
      <Alert>
        <AlertDescription>
          Access denied. Buyer privileges required.
        </AlertDescription>
      </Alert>
    )
  }

  return (
    <div className="space-y-8">
      <div className="flex justify-between items-center">
        <div>
          <h1 className="text-3xl font-bold text-gray-900">Shop</h1>
          <p className="text-gray-600 mt-2">Discover amazing pre-loved items</p>
        </div>
        <div className="flex space-x-2">
          <Dialog open={isRequestingItem} onOpenChange={setIsRequestingItem}>
            <DialogTrigger asChild>
              <Button variant="outline">Request Item</Button>
            </DialogTrigger>
            <DialogContent>
              <DialogHeader>
                <DialogTitle>Request an Item</DialogTitle>
                <DialogDescription>
                  Can't find what you're looking for? Request it from our sellers.
                </DialogDescription>
              </DialogHeader>
              <div className="space-y-4">
                <div className="space-y-2">
                  <Label htmlFor="requestTitle">Item Title</Label>
                  <Input
                    id="requestTitle"
                    placeholder="What are you looking for?"
                    value={newRequest.title}
                    onChange={(e) => setNewRequest({...newRequest, title: e.target.value})}
                  />
                </div>
                
                <div className="space-y-2">
                  <Label htmlFor="requestDescription">Description</Label>
                  <Textarea
                    id="requestDescription"
                    placeholder="Provide more details..."
                    value={newRequest.description}
                    onChange={(e) => setNewRequest({...newRequest, description: e.target.value})}
                  />
                </div>
                
                <div className="grid grid-cols-2 gap-4">
                  <div className="space-y-2">
                    <Label htmlFor="requestCategory">Category</Label>
                    <Select value={newRequest.category} onValueChange={(value) => setNewRequest({...newRequest, category: value})}>
                      <SelectTrigger>
                        <SelectValue placeholder="Select category" />
                      </SelectTrigger>
                      <SelectContent>
                        <SelectItem value="Electronics">Electronics</SelectItem>
                        <SelectItem value="Fashion">Fashion</SelectItem>
                        <SelectItem value="Furniture">Furniture</SelectItem>
                        <SelectItem value="Books">Books</SelectItem>
                        <SelectItem value="Sports">Sports</SelectItem>
                        <SelectItem value="Other">Other</SelectItem>
                      </SelectContent>
                    </Select>
                  </div>
                  
                  <div className="space-y-2">
                    <Label htmlFor="requestBudget">Budget ($)</Label>
                    <Input
                      id="requestBudget"
                      type="number"
                      placeholder="0.00"
                      value={newRequest.budget}
                      onChange={(e) => setNewRequest({...newRequest, budget: e.target.value})}
                    />
                  </div>
                </div>
                
                <div className="space-y-2">
                  <Label htmlFor="requestDuration">Duration (for rentals)</Label>
                  <Input
                    id="requestDuration"
                    placeholder="e.g., 3 days, 1 week (optional)"
                    value={newRequest.duration}
                    onChange={(e) => setNewRequest({...newRequest, duration: e.target.value})}
                  />
                </div>
                
                <div className="flex justify-end space-x-2">
                  <Button variant="outline" onClick={() => setIsRequestingItem(false)}>
                    Cancel
                  </Button>
                  <Button onClick={handleItemRequest}>
                    Submit Request
                  </Button>
                </div>
              </div>
            </DialogContent>
          </Dialog>
          
          <Dialog open={isCheckingOut} onOpenChange={setIsCheckingOut}>
            <DialogTrigger asChild>
              <Button disabled={cart.length === 0}>
                Cart ({cart.length})
              </Button>
            </DialogTrigger>
            <DialogContent>
              <DialogHeader>
                <DialogTitle>Shopping Cart</DialogTitle>
                <DialogDescription>
                  Review your items and checkout
                </DialogDescription>
              </DialogHeader>
              <div className="space-y-4">
                {cart.map((item) => (
                  <div key={item.id} className="flex justify-between items-center p-3 border rounded">
                    <div>
                      <h4 className="font-medium">{item.product.title}</h4>
                      <p className="text-sm text-gray-600">
                        {item.type === 'rental' ? `Rental (${item.rentalDays} days)` : 'Purchase'}
                      </p>
                      <p className="text-sm font-medium">
                        ${item.type === 'rental' && item.product.rentalPrice && item.rentalDays
                          ? item.product.rentalPrice * item.rentalDays
                          : item.product.price}
                      </p>
                    </div>
                    <Button size="sm" variant="destructive" onClick={() => removeFromCart(item.id)}>
                      Remove
                    </Button>
                  </div>
                ))}
                
                {cart.length > 0 && (
                  <>
                    <div className="border-t pt-4">
                      <div className="flex justify-between font-bold">
                        <span>Total: ${calculateCartTotal()}</span>
                      </div>
                    </div>
                    
                    <div className="space-y-2">
                      <Label htmlFor="deliveryAddress">Delivery Address</Label>
                      <Textarea
                        id="deliveryAddress"
                        placeholder="Enter your delivery address"
                        value={deliveryAddress}
                        onChange={(e) => setDeliveryAddress(e.target.value)}
                      />
                    </div>
                    
                    <div className="flex justify-end space-x-2">
                      <Button variant="outline" onClick={() => setIsCheckingOut(false)}>
                        Continue Shopping
                      </Button>
                      <Button onClick={handleCheckout} disabled={!deliveryAddress}>
                        Checkout
                      </Button>
                    </div>
                  </>
                )}
              </div>
            </DialogContent>
          </Dialog>
        </div>
      </div>

      {/* Search and Filters */}
      <div className="flex flex-col md:flex-row gap-4">
        <div className="flex-1">
          <Input
            placeholder="Search items..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
          />
        </div>
        <Select value={categoryFilter} onValueChange={setCategoryFilter}>
          <SelectTrigger className="w-48">
            <SelectValue placeholder="All Categories" />
          </SelectTrigger>
          <SelectContent>
            <SelectItem value="all">All Categories</SelectItem>
            <SelectItem value="Electronics">Electronics</SelectItem>
            <SelectItem value="Fashion">Fashion</SelectItem>
            <SelectItem value="Furniture">Furniture</SelectItem>
            <SelectItem value="Books">Books</SelectItem>
            <SelectItem value="Sports">Sports</SelectItem>
          </SelectContent>
        </Select>
        <Select value={typeFilter} onValueChange={setTypeFilter}>
          <SelectTrigger className="w-32">
            <SelectValue placeholder="All Types" />
          </SelectTrigger>
          <SelectContent>
            <SelectItem value="all">All</SelectItem>
            <SelectItem value="buy">Buy</SelectItem>
            <SelectItem value="rent">Rent</SelectItem>
          </SelectContent>
        </Select>
      </div>

      {/* Main Content Tabs */}
      <Tabs defaultValue="browse" className="space-y-6">
        <TabsList>
          <TabsTrigger value="browse">Browse Items</TabsTrigger>
          <TabsTrigger value="wishlist">Wishlist ({wishlist.length})</TabsTrigger>
          <TabsTrigger value="orders">My Orders</TabsTrigger>
          <TabsTrigger value="requests">My Requests</TabsTrigger>
        </TabsList>

        <TabsContent value="browse">
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
            {filteredProducts.map((product) => (
              <Card key={product.id} className="hover:shadow-lg transition-shadow">
                <CardHeader>
                  <div className="aspect-square bg-gray-100 rounded-lg mb-4 flex items-center justify-center">
                    <span className="text-gray-400 text-sm">Product Image</span>
                  </div>
                  <div className="flex justify-between items-start">
                    <div className="flex-1">
                      <CardTitle className="text-lg">{product.title}</CardTitle>
                      <CardDescription className="mt-1">
                        <span className="text-sm text-gray-500">{product.category}</span>
                        <span className="mx-2">•</span>
                        <span className="text-sm text-green-600">{product.condition}</span>
                      </CardDescription>
                      <p className="text-sm text-gray-600 mt-2">{product.description}</p>
                      <p className="text-xs text-gray-500 mt-1">Sold by {product.seller}</p>
                    </div>
                    <Button
                      size="sm"
                      variant="ghost"
                      onClick={() => toggleWishlist(product.id)}
                      className={wishlist.includes(product.id) ? 'text-red-500' : 'text-gray-400'}
                    >
                      ♥
                    </Button>
                  </div>
                </CardHeader>
                <CardContent>
                  <div className="space-y-3">
                    <div className="flex justify-between items-center">
                      <span className="text-2xl font-bold text-gray-900">${product.price}</span>
                      {product.availability === 'available' && (
                        <Button 
                          size="sm"
                          onClick={() => addToCart(product, 'purchase')}
                        >
                          Add to Cart
                        </Button>
                      )}
                      {product.availability !== 'available' && (
                        <Badge variant="secondary">{product.availability}</Badge>
                      )}
                    </div>
                    
                    {product.rentalPrice && product.availability === 'available' && (
                      <div className="flex justify-between items-center pt-2 border-t">
                        <span className="text-lg font-medium text-blue-600">
                          ${product.rentalPrice}/day
                        </span>
                        <Button 
                          size="sm" 
                          variant="outline"
                          onClick={() => addToCart(product, 'rental', 3)}
                        >
                          Rent (3 days)
                        </Button>
                      </div>
                    )}
                  </div>
                </CardContent>
              </Card>
            ))}
          </div>
        </TabsContent>

        <TabsContent value="wishlist">
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
            {products.filter(p => wishlist.includes(p.id)).map((product) => (
              <Card key={product.id} className="hover:shadow-lg transition-shadow">
                <CardHeader>
                  <div className="aspect-square bg-gray-100 rounded-lg mb-4 flex items-center justify-center">
                    <span className="text-gray-400 text-sm">Product Image</span>
                  </div>
                  <CardTitle className="text-lg">{product.title}</CardTitle>
                  <CardDescription>{product.description}</CardDescription>
                </CardHeader>
                <CardContent>
                  <div className="flex justify-between items-center">
                    <span className="text-2xl font-bold text-gray-900">${product.price}</span>
                    <div className="flex space-x-2">
                      <Button size="sm" onClick={() => addToCart(product, 'purchase')}>
                        Add to Cart
                      </Button>
                      <Button 
                        size="sm" 
                        variant="outline"
                        onClick={() => toggleWishlist(product.id)}
                      >
                        Remove
                      </Button>
                    </div>
                  </div>
                </CardContent>
              </Card>
            ))}
          </div>
        </TabsContent>

        <TabsContent value="orders">
          <Card>
            <CardHeader>
              <CardTitle>Order History</CardTitle>
              <CardDescription>Track your purchases and rentals</CardDescription>
            </CardHeader>
            <CardContent>
              <div className="space-y-4">
                {orders.map((order) => (
                  <div key={order.id} className="border rounded-lg p-4">
                    <div className="flex justify-between items-start mb-2">
                      <div>
                        <h4 className="font-medium">Order #{order.id}</h4>
                        <p className="text-sm text-gray-600">{order.date}</p>
                      </div>
                      <Badge 
                        variant={
                          order.status === 'delivered' ? 'default' :
                          order.status === 'pending' ? 'secondary' : 'outline'
                        }
                      >
                        {order.status}
                      </Badge>
                    </div>
                    <p className="text-sm text-gray-600 mb-2">
                      Delivery: {order.deliveryAddress}
                    </p>
                    <div className="flex justify-between items-center">
                      <span className="font-bold">Total: ${order.total}</span>
                      <Button size="sm" variant="outline">View Details</Button>
                    </div>
                  </div>
                ))}
              </div>
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="requests">
          <Card>
            <CardHeader>
              <CardTitle>Item Requests</CardTitle>
              <CardDescription>Your requested items and their status</CardDescription>
            </CardHeader>
            <CardContent>
              <div className="space-y-4">
                {requests.map((request) => (
                  <div key={request.id} className="border rounded-lg p-4">
                    <div className="flex justify-between items-start mb-2">
                      <div>
                        <h4 className="font-medium">{request.title}</h4>
                        <p className="text-sm text-gray-600">{request.description}</p>
                        <p className="text-sm text-gray-500 mt-1">
                          {request.category} • Budget: ${request.budget}
                          {request.duration && ` • Duration: ${request.duration}`}
                        </p>
                      </div>
                      <Badge 
                        variant={
                          request.status === 'fulfilled' ? 'default' :
                          request.status === 'open' ? 'secondary' : 'outline'
                        }
                      >
                        {request.status}
                      </Badge>
                    </div>
                    <p className="text-xs text-gray-500">Requested on {request.date}</p>
                  </div>
                ))}
              </div>
            </CardContent>
          </Card>
        </TabsContent>
      </Tabs>
    </div>
  )
}
